import EasyPieChart from "easy-pie-chart";

export function progress() {
  const charts = document.querySelectorAll(".skills__chart");

  const chartsObserver = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        const elem = entry.target;
        const color = elem.getAttribute("data-color");
        const icon = elem.querySelector("i");
        if (icon) {
          icon.style.color = color;
        }
        elem.parentElement.style.boxShadow = `0px 0px 40px ${color}`;
        new EasyPieChart(elem, {
          size: 160,
          barColor: color,
          scaleLength: 0,
          lineWidth: 12,
          trackColor: "#373737",
          lineCap: "round",
          animate: 2000,
        });
      }
    });
  });

  charts.forEach((elem) => chartsObserver.observe(elem));
}
